import React, { Component } from 'react';
import { Navbar,Nav,NavDropdown,Form,FormControl,Button } from 'react-bootstrap';

class Header extends Component {
    render() {
        return (
            <Navbar bg="light" expand="lg" variant="light">
                <Navbar.Brand href="#home">Sea Dive</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto">
                        <Nav.Link href="#home">Home</Nav.Link>
                       
                        <NavDropdown title="Activities" id="basic-nav-dropdown">
                            <NavDropdown.Item href="/scuba">Scuba Diving</NavDropdown.Item>
                            <NavDropdown.Item href="/jetski">Jet Ski</NavDropdown.Item>
                            <NavDropdown.Item href="/parasailing">Para Sailing</NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item href="/bananaride">Banana Ride</NavDropdown.Item>
                        </NavDropdown>
                    </Nav>
                    <Form inline>
                        <FormControl type="text" placeholder="Search" className="mr-sm-2" />
                        <Button variant="outline-success">Search</Button>
                    </Form>
                </Navbar.Collapse>
            </Navbar>
        );
    }
}

export default Header;