import React, { Component } from 'react';
import {Jumbotron,Container} from 'react-bootstrap';
import "./Herospace.css";
import CoverImage from '../../assets/cover_one.jpg';

class Herospace extends Component {
    render() {
        return (
            <Jumbotron fluid style={{backgroundImage:`url(${CoverImage})`}}> 
            <Container>
              <h1 style={{fontSize:'5.5rem',color:'white'}}>Sea Dive</h1>
              <p style={{fontSize:'1.9rem',color:'white'}}>
               Life looks better underwater !
              </p>
            </Container>
          </Jumbotron>
        );
    }
}

export default Herospace;