import React, { Component } from 'react';
import { CardDeck, Card ,Button} from 'react-bootstrap';
import './Card.css';
import Scuba from '../../assets/scuba.jpg';
import JetSki from '../../assets/jetski.jpg';
import Parasailing from '../../assets/parasailing.webp';
import Banana from '../../assets/banana.jpg';


class CardsGroup extends Component {
    render() {
        return (
            <CardDeck >
                <Card >
                    <Card.Img variant="top" src={Scuba} />
                    <Card.Body>
                        <Card.Title>Scuba Diving</Card.Title>
                        <Card.Text>
                            Come experience a Life time Thrill and explore a new world.
                        </Card.Text>
                    </Card.Body>
                    <Card.Footer>
                    <Button variant="primary">See More</Button>
                    </Card.Footer>
                </Card>
                <Card>
                    <Card.Img variant="top" src={JetSki} />
                    <Card.Body>
                        <Card.Title>Jet Ski</Card.Title>
                        <Card.Text>
                           Come ride with us through the sea !
                        </Card.Text>
                    </Card.Body>
                    <Card.Footer>
                    <Button variant="primary">See More</Button>
                    </Card.Footer>
                </Card>
                <Card>
                    <Card.Img variant="top" src={Banana} />
                    <Card.Body>
                        <Card.Title>Banana Ride</Card.Title>
                        <Card.Text>
                            Come sail with us with a fall and experience the beauty of sea .
                        </Card.Text>
                    </Card.Body>
                    <Card.Footer>
                    <Button variant="primary">See More</Button>
                    </Card.Footer>
                </Card>
                <Card>
                    <Card.Img variant="top" src={Parasailing} />
                    <Card.Body>
                        <Card.Title>Para Sailing</Card.Title>
                        <Card.Text>
                            Fly with us to see the beautiful view of this mesmerizing nature !
                        </Card.Text>
                    </Card.Body>
                    <Card.Footer>
                    <Button variant="primary">See More</Button>
                    </Card.Footer>
                </Card>
            </CardDeck>
        );
    }
}

export default CardsGroup;