import React, { Component } from 'react';

import ContactImage1 from '../../assets/contact_vicky.jpeg';

class Contacts extends Component {
    render() {
        return (
            
            <div className="container"style={{textAlign:"center"}}>
                <h1 className="col-12 col-sm-12 col-md-6 px-0" style={{fontSize:"3rem"}}>Contacts</h1>
                <div className="card card-default" id="card_contacts">
                    <div id="contacts" className="panel-collapse collapse show" aria-expanded="true" >
                        <ul className="list-group pull-down" id="contact-list">
                            <li className="list-group-item">
                                <div className="row w-100">
                                    <div className="col-12 col-sm-6 col-md-3 px-0">
                                        <img src={ContactImage1} alt="Mike Anamendolla" className="rounded-circle mx-auto d-block img-fluid" style={{height:"80%"}}/>
                                    </div>
                                    <div className="col-12 col-sm-6 col-md-9 text-center text-sm-left">
                                        <span className="fa fa-mobile fa-2x text-success float-right pulse" title="online now"></span>
                                        <label className="name lead">Vicky Shinde</label>
                                        <br />
                                        <span className="fa fa-map-marker fa-fw text-muted" data-toggle="tooltip" title="" data-original-title="5842 Hillcrest Rd"></span>
                                        <span className="text-muted">Malvan, Maharastra</span>
                                        <br />
                                        <span className="fa fa-phone fa-fw text-muted" data-toggle="tooltip" title="" data-original-title="(870) 288-4149"></span>
                                        <span className="text-muted small">+91 8261822106</span>
                                        <br />
                                        <span className="fa fa-envelope fa-fw text-muted" data-toggle="tooltip" data-original-title="" title=""></span>
                                        <span className="text-muted small text-truncate">vickyshinde@seadive.com</span>
                                    </div>
                                </div>
                            </li>
                        </ul>

                    </div>
                </div>
                </div>
            
        );
    }
}

export default Contacts;